.watch-of-Choice {
  padding: 60px 0;

  .watch__content {
    display: flex;
    justify-content: space-between;
  }

  .info {
    width: 50%;
    padding: 15px;
    box-sizing: border-box;
    padding-right: 40px;
    display: flex;
    align-items: center;

    .info-content {
      h2 {
        font-size: 55px;
        font-weight: 800;
        margin: 0;
        margin-bottom: 35px;
        color: var(--title-color);
        text-transform: capitalize;
      }

      p {
        line-height: 1.6;
        width: 90%;
        font-size: 18px;
        margin: 0;
        color: var(--text-color);
        margin-bottom: 17px;
      }

      a {
        text-transform: uppercase;
        text-decoration: none;
        color: #fff;
        background-color: var(--main-color);
        padding: 18px 45px;
        font-weight: 600;
        font-size: 16px;
        margin-top: 26px;
        display: inline-block;
      }
    }
  }

  .card {
    width: 50%;
    padding-left: 40px;

    box-sizing: border-box;
    img {
      width: 100%;
    }
  }
}
@media #{$information-large-desktops} {
  .watch-of-Choice {
    padding: 60px 0;

    .watch__content {
      display: flex;
      justify-content: space-between;
    }

    .info {
      width: 50%;
      padding: 15px;
      box-sizing: border-box;
      padding-right: 40px;
      display: flex;
      align-items: center;

      .info-content {
        h2 {
          font-size: 45px;
          font-weight: 800;
          margin: 0;
          margin-bottom: 35px;
          color: var(--title-color);
          text-transform: capitalize;
