    }
  }
}
@media #{$information-desktops} {
  .watch-of-Choice {
    padding: 45px 0;
    .watch__content {
      flex-wrap: wrap;
    }
    .info {
      width: 50%;
      padding: 15px 0;
      padding-left: 0;
      padding-right: 21px;
      .info-content {
        h2 {
          font-size: 30px;
          margin-bottom: 12px;
        }

        p {
          line-height: 1.6;
          width: 100%;
          font-size: 13px;
          margin-bottom: 0;
        }

        a {
          padding: 13px 25px;
          font-weight: 700;
          font-size: 10px;
          margin-top: 26px;
        }
      }
    }

    .card {
      width: 50%;
      padding-left: 0;
      img {
        width: 100%;
      }
    }
  }
}
@media (min-width: 576px) and (max-width: 767.98px) {
  .watch-of-Choice {
    padding: 35px 0;

    .watch__content {
      flex-wrap: wrap;
    }

    .info {
      width: 100%;
      padding: 0;
      padding-top: 25px;
      padding-right: 0;
      order: 2;

      .info-content {
        h2 {
          font-size: 30px;
          margin-bottom: 15px;
        }

        p {
          line-height: 1.5;
          width: 95%;
          font-size: 14px;
        }

        a {
          padding: 14px 32px;
          font-weight: 700;
          font-size: 11px;
          margin-top: 21px;
        }
      }
    }

    .card {
      width: 100%;
      padding-left: 0;

      img {
        width: 100%;
      }
    }
  }
}

