.popular-items {
  padding: 60px 0;
  text-align: center;
  .popular__title {
    text-align: center;
    h2 {
      text-transform: capitalize;
      font-size: 50px;
      margin: 20px 0;
      color: var(--title-color);
    }
    p {
      width: 55%;
      font-weight: 600;
      color: var(--text-color);
      line-height: 1.7;
      margin: auto;
    }
  }
  .popular__content {
    margin: 50px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .popular__more {
    text-transform: capitalize;
    text-decoration: none;
    color: #fff;
    background-color: var(--main-color);
    padding: 22px 45px;
    font-weight: 600;
    font-size: 18px;
    margin-top: 26px;
    display: inline-block;
  }
}
@media #{$information-large-desktops} {
  .popular-items {
    padding: 60px 0;
    text-align: center;
    .popular__title {
      text-align: center;
      h2 {
        font-size: 43px;
      }
      p {
        font-size: 14px;
      }
    }
    .popular__more {
      padding: 16px 32px;

      font-size: 13px;
    }
  }
}
@media #{$information-desktops} {
  .popular-items {
    padding: 45px 0;
    .popular__title {
      h2 {
        font-size: 37px;
        margin: 20px 0;
      }

      p {
        width: 75%;
        line-height: 1.5;
        font-size: 15px;
      }
    }

    .popular__content {
      flex-wrap: wrap;
      margin: 39px 0;
    }
    .popular__more {
      padding: 15px 32px;
      font-weight: 600;
      font-size: 13px;
    }
  }
}
@media #{$information-tablet} {
  .popular-items {
    padding: 35px 0;
    .popular__title {
      h2 {
        font-size: 38px;
        margin: 20px 0;
      }
      p {
        width: 75%;
        line-height: 1.5;
        font-size: 14px;
      }
    }
    .popular__more {
      padding: 14px 32px;
      font-weight: 600;
      font-size: 11px;
    }
  }
}
@media #{$information-phone} {
  .popular-items {
    padding: 35px 0;
    .popular__title {
      h2 {
        font-size: 27px;
        margin: 10px 0;
      }
      p {
        width: 95%;
        line-height: 1.5;
        font-size: 13px;
      }
    }
    .popular__content {
      margin: 27px 0;
      margin-bottom: 0;
      justify-content: center;
    }
    .popular__more {
      padding: 14px 24px;
      font-weight: 600;
      font-size: 11px;
      margin-top: 0;
    }
  }
}
