.video {
  padding: 60px 0;

  .video__contianer {
    width: 100%;
    height: 700px;
    background-image: url('../img/gallery/sectin_bg1.webp');
    position: relative;
    background-size: cover;
    background-position: center;

    .play {
      color: #fff;
      font-size: 85px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    .video__next {
      border: none;
      position: absolute;
      bottom: 0;
      left: 0;
      background-color: var(--main-color);
      color: #fff;
      padding: 57px 0px;
      width: 400px;
      font-weight: 600;
      text-transform: uppercase;
      font-size: 18px;
      outline: none;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: space-around;

      .btn__icon {
        font-size: 30px;
      }
    }
  }
}
@media #{$information-large-desktops} {
  .video {
    padding: 60px 0;

    .video__contianer {
