.pagination {
  width: 406px;
  margin: auto;
  height: 38px;
  padding: 3px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .pagination_icon {
    width: 20%;
    align-items: center;
    height: 100%;
    font-size: 22px;
    border: 1px solid var(--mode-border);
    color: var(--text-color);
    display: flex;
    justify-content: center;
  }
  .navs {
    display: none;
  }
  .navs-mobile {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    font-size: 12px;
    text-transform: capitalize;
    div {
      width: 33%;
      height: 100%;
      color: var(--text-color);
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 26px;
      font-weight: 600;
      .slash {
        font-weight: 700;
        transform: rotateZ(81deg);
      }
    }
    .number-page--start {
      padding-right: 14px;
      input {
        width: 81px;
        height: 100%;
        box-sizing: border-box;
        padding: 4px 4px;
        text-align: center;
        border: 1px solid var(--mode-border);
        color: #333;
        outline: none;
      }
    }
  }
}
@media #{$information-phone} {
  .pagination {
    width: 210px;
    margin: auto;
    height: 26px;
    padding: 3px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .pagination_icon {
      width: 20%;
      align-items: center;
      height: 100%;
      font-size: 13px;
      border: 1px solid var(--mode-border);
      color: var(--text-color);
      display: flex;
      justify-content: center;
    }
    .navs,
    .nav {
      display: none;
    }
    .navs-mobile {
      width: 50%;
      height: 100%;
      display: flex;
      align-items: center;
      font-size: 12px;
      text-transform: capitalize;
      div {
        width: 33%;
        height: 100%;
        color: var(--text-color);
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 12px;
        font-weight: 600;
        .slash {
          font-weight: 700;
          transform: rotateZ(81deg);
        }
      }
      .number-page--start {
        padding-right: 14px;
        input {
          width: 33px;
          height: 100%;
          box-sizing: border-box;
          padding: 4px 4px;
          text-align: center;
          border: 1px solid var(--mode-border);
          color: #333;
          outline: none;
        }
      }
    }
  }
}
