.progress-stars {
  position: relative;
  width: max-content;
  &::before {
    content: '\f005 \f005 \f005 \f005 \f005';
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    color: #ddd;
    font-size: 17px;
  }
  ul {
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    margin: 0;
    justify-content: space-between;
    overflow: hidden;
    padding: 0;
    display: flex;
    list-style: none;
    li {
      display: block !important;
      margin: 0 !important;
      padding: 0;
      .watch__star {
        font-size: 17px;
        color: #ffdf00;
      }
    }
  }
}
