.prduct--details__wapper {
  display: flex;
}
.product--info {
  padding: 32px 0;
  .product_images {
    width: 50%;
    .imgs-contailer {
      height: 570px;
      position: relative;
      .img-box {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: none;
        box-sizing: border-box;
        padding: 20px 40px;
        img {
          width: 100%;
          height: 100%;
        }
        &.active {
          display: block;
        }
      }
    }
    .imgs-navigation {
      display: flex;
      height: 132px;
      .img-nav {
        box-sizing: border-box;
        padding: 15px;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .product--details__data {
    padding: 30px 0;
    width: 50%;
    .h2 {
      font-size: 31px;
      color: var(--title-color);
    }
    .review {
      border-bottom: 1px solid var(--mode-border);
      padding: 15px 0;
      display: flex;
      justify-content: flex-start;
      .stars {
        display: flex;
        padding-right: 15px;
        align-items: center;
        border-right: 1px solid var(--mode-border);
        .watch__star {
          overflow: hidden;
          color: #ffdf00;
        }
        .progress-stars {
          position: relative;
          width: max-content;
          &::before {
            content: '\f005 \f005 \f005 \f005 \f005';
            font-family: 'Font Awesome 5 Free';
            font-weight: 900;
            color: #ddd;
            font-size: 17px;
          }
          ul {
            position: absolute;
            top: 0;
            left: 0;
            width: 0px;
            height: 100%;
            overflow: hidden;
            padding: 0;
            margin: 0;
            list-style: none;
            display: flex;
            li {
              .watch__star {
                font-size: 17px;
                overflow: hidden;
                color: #ffdf00;
              }
            }
          }
        }

        .review-count {
          font-size: 17px;
          text-transform: capitalize;
          color: var(--text-color);
          line-height: 1;
          padding-left: 9px;
        }
      }
      .write-review {
        font-size: 17px;
        padding-left: 15px;
        box-sizing: border-box;
        color: var(--text-color);
        display: flex;
        align-items: center;
        span {
          text-transform: capitalize;
        }
        .pen {
          margin-right: 11px;
        }
      }
    }
    .table-data {
      padding: 15px 0;
      border-bottom: 1px solid var(--mode-border);
      table {
        font-size: 16px;
        color: var(--text-color);
        td.value {
          padding: 8px;
        }
      }
    }
    .details-product_option {
      padding: 15px 0;
      .details_color {
        width: 100%;
        position: relative;
        height: 56px;
      }
      .add-cart {
        border: none;
        color: #fff;
        background: var(--main-color);
        text-transform: capitalize;
        font-size: 16px;
        padding: 12px 32px;
        outline: none;
      }
    }
    .details_price {
      padding: 18px 0;
      border-bottom: 1px solid var(--mode-border);
      span {
        font-size: 20px;
        letter-spacing: 2px;
        color: var(--main-color);
      }
    }
  }
}
.description-review {
  margin-top: 50px;
  .head {
    display: flex;
    justify-content: space-around;
    padding: 6px 0;
    font-size: 22px;
    border-bottom: 1px solid var(--mode-border);
    color: var(--text-color);
    .head-desc {
      width: 50%;
      text-align: center;
      border-right: 1px solid var(--mode-border);
      text-transform: capitalize;
      cursor: pointer;
    }
    .head-review {
      width: 50%;
      text-align: center;
      text-transform: capitalize;
      cursor: pointer;
    }
  }
  .description {
    padding: 5px 0;
    display: none;
    h3 {
      text-transform: capitalize;
      font-size: 23px;
      margin-bottom: 8px;
      font-weight: 400;
      margin-top: 13px;
      color: var(--title-color);
    }
    ol {
      padding-left: 22px;
      margin: 0;
      font-size: 16px;
      line-height: 1.5;
      color: var(--text-color);
    }
    .description_paragraph {
      p {
        margin: 0;
        font-size: 16px;
        line-height: 1.5;
        width: 80%;
        color: var(--text-color);
      }
    }
    .Specifications {
      .desc_table {
        font-size: 15px;
        width: 100%;
        tr {
          &:nth-child(odd) {
            background-color: var(--mode-border);
          }
        }
        td {
          padding: 5px 0;
          color: var(--text-color);
          line-height: 1.5;
          &.value {
            padding-left: 12px;
          }
          &.specification_key {
            width: 185px;
            padding-left: 3px;
          }
        }
      }
    }
  }
  .review {
    display: none;
    .review_content {
      padding: 16px 0;
      border-bottom: 1px solid var(--mode-border);
      .comment-name {
        display: flex;
        justify-content: space-between;
        padding-bottom: 9px;
        span {
          font-size: 20px;
          color: var(--title-color);
          &.date {
            font-size: 14px;
            color: var(--text-color);
          }
        }
      }
      .comment-desc {
        p {
          margin: 0;
          font-size: 15px;
          width: 66%;
          line-height: 1.5;
          color: var(--text-color);
        }
      }
      ul {
        padding: 0;
        margin: 0;
        list-style: none;
        display: flex;
        font-size: 10px;
        padding-top: 6px;
        color: #ffdf00;
      }
    }
    .write-review-form {
      padding: 15px 0;
      form {
        input[type='text'] {
          width: 100%;
          border: 1px solid var(--mode-border);
          padding: 16px;
          box-sizing: border-box;
          margin-bottom: 16px;
          color: var(--text-color);
          background: var(--mode-background);
          font-size: 14px;
          outline: none;
        }
        textarea {
          overflow: auto;
          width: 100%;
          border: 1px solid var(--mode-border);
          padding: 16px;
          color: var(--text-color);
          box-sizing: border-box;
          margin-bottom: 11px;
          resize: none;
          height: 120px;
          background: var(--mode-background);
          font-size: 14px;
          outline: none;
        }
        ::placeholder {
          text-transform: capitalize;
          color: var(--text-color);
          font-size: 14px;
        }
        .rating {
          display: flex;
          input[type='radio'] {
            margin-right: 7px;
          }
          .rating_title {
            text-transform: capitalize;
            font-size: 13px;
            font-weight: 400;
            margin-right: 13px;
            color: var(--text-color);
          }
          margin-bottom: 15px;
        }
        button {
          border: none;
          color: #fff;
          margin-top: 15px;
          display: inline-block;
          background: var(--main-color);
          text-transform: capitalize;
          font-size: 16px;
          padding: 11px 32px;
          font-weight: 700;
        }
      }
    }
  }
  .active {
    display: block;
  }
}
