.post {
  box-shadow: 0 0 7px 0px rgba(0, 0, 0, 0.06);
  margin-bottom: 45px;

  .post__img {
    height: 372px;
    overflow: hidden;

    img {
      width: 100%;
    }
  }

  .post__info {
    position: relative;
    padding: 51px 42px;
    padding-bottom: 35px;

    .date {
      position: absolute;
      background: var(--main-color);
      color: #fff;
      padding: 25px 34px;
      text-transform: capitalize;
      font-size: 34px;
      font-weight: 700;
      border-radius: 8px;
      box-shadow: 0px 6px 20px 3px rgba(0, 0, 0, 0.2);
      top: 0;
      left: 38px;
      transform: translateY(-85%);
      text-align: center;

      .month {
        display: block;
        font-size: 22px;
      }
    }

    .post__title {
      text-decoration: none;
      color: var(--title-color);
      font-size: 25px;
      font-weight: 700;
    }

    p {
      width: 93%;
      line-height: 1.6;
      font-size: 18px;
      color: var(--text-color);
      margin-bottom: 33px;
    }

    .Interaction {
      a {
        text-transform: capitalize;
        text-decoration: none;
        color: var(--main-color);
        margin-right: 6px;
        padding-right: 3px;

        .icon {
          margin-right: 3px;
        }
      }
    }
  }
}
@media #{$information-large-desktops} {
  .post {
    .post__info {
      .date {
        position: absolute;
        background: var(--main-color);
        color: #fff;
        padding: 27px 18px;
        text-transform: capitalize;
        font-size: 25px;
        font-weight: 700;
        border-radius: 8px;
        box-shadow: 0px 6px 20px 3px rgba(0, 0, 0, 0.2);
        top: 0;
        left: 38px;
        transform: translateY(-85%);
        text-align: center;

        .month {
          display: block;
          font-size: 17px;
        }
      }

      .post__title {
        font-size: 22px;
      }

      p {
        font-size: 15px;
      }

      .Interaction {
        a {
          font-size: 13px;
        }
      }
    }
  }
}
