header {
  z-index: 90;
  position: fixed;
  width: 100%;
  top: 0;
  background-color: var(--mode-background);
  box-shadow: 0px 1px 20px 0 rgba(0, 0, 0, 0.1);

  .head__container {
    max-width: 1600px;
    padding: 0 30px;
    margin: auto;
  }

  .row {
    display: flex;
    padding: 38px 0;

    .nav__logo {
      width: 25%;
      display: flex;
      align-items: center;

      .logo--icon {
        margin-right: 3px;
      }

      h1 {
        margin: 0;

        a {
          text-decoration: none;
          color: var(--title-color);
        }
        span {
          color: var(--main-color);
          margin-left: 5px;
        }
      }
    }

    .head__btn {
      display: none;
      .btn__icon {
        font-size: 20px;
        cursor: pointer;
        color: var(--title-color);
        position: relative;
        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          width: 100%;
          z-index: 55;
        }
      }
    }

    .head__nav-login {
      width: 75%;
      display: flex;

      nav {
        display: flex;
        align-items: center;
        width: 68%;
        justify-content: center;

        .nav__list {
          padding: 0;
          margin: 0;
          list-style: none;
          align-items: center;
          text-transform: capitalize;
          width: max-content;

          .list__item {
            display: inline-block;
            margin-right: 35px;

            a {
              text-decoration: none;
              color: var(--title-color);
              font-size: 17px;
              font-weight: 700;
              font-family: 'Josefin Sans', sans-serif;
              &.link-color {
                color: var(--main-color);
              }
            }
          }
        }
      }

      .head__icon {
        width: 32%;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        a {
          .icon {
            margin-left: 31px;
            color: var(--main-color);
            position: relative;
            text-decoration: none;
            cursor: pointer;
          }
        }

        .count-products {
          position: absolute;
          color: #fff;
          background-color: #00f;
          font-size: 15px;
          top: -18px;
          width: 18px;
          height: 18px;
          padding-top: 3px;
          border-radius: 3px;
          display: flex;
          justify-content: center;
          font-weight: 700;
          right: -18px;
          box-sizing: border-box;
          align-items: center;
        }
      }
    }
  }

  .border--row {
    width: 50%;
    height: 5px;
    background-color: var(--main-color);
    bottom: 0;
  }

  .right {
    position: absolute;
    right: 0;
  }

  .left {
    position: absolute;
    left: 0;
  }
}

