@media #{$information-desktops} {
  header {
    .row {
      padding: 24px 0;

      .nav__logo {
        width: 50%;

        h1 {
          font-size: 25px;
        }
      }

      .head__btn {
        display: flex;
        width: 50%;
        justify-content: flex-end;
        align-items: center;

        .btn__icon {
          font-size: 25px;
          cursor: pointer;
        }
      }

      .head__nav-login {
        position: absolute;
        background-color: var(--mode-background);
        top: 100%;
        width: 435px;
        left: 50%;
        max-height: 370px;
        right: 0;
        display: flex;
        box-sizing: border-box;
        flex-wrap: wrap;
        transform: rotateY(90deg) translateX(-50%);
        padding: 15px;
        height: 65vh;
        transform-origin: center;
        perspective: 100px;
        transition: transform 0.2s ease-in-out;
        &.show {
          transform: rotateY(0deg) translateX(-50%);
        }

        nav {
          width: 100%;

          .nav__list {
            width: 80%;

            .list__item {
              margin-bottom: 10px;
              border-bottom: 1px solid var(--mode-border);
              padding-bottom: 10px;
              text-align: center;
              display: block;
              margin-right: 0;

              &:last-child {
                margin-bottom: 0;
                padding-bottom: 0;
                border-bottom: none;
              }

              a {
                text-transform: uppercase;
                font-size: 14px;
                font-weight: 500;
                color: var(--title-color);
              }
            }
          }
        }

        .head__icon {
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          margin: auto;

          .icon {
            margin: 0 15px;
            color: var(--main-color);
            position: relative;
            text-decoration: none;
            cursor: pointer;
            font-size: 13px;
          }
        }
      }
    }
  }
}

