.postDetails {
  width: 67%;
  display: flex;
  flex-wrap: wrap;

  & > div {
    width: 100%;
  }

  .post {
    box-shadow: none;
    margin-bottom: 0px;

    .post__info {
      padding: 16px 24px;
      padding-left: 0;

      p {
        width: 93%;
        line-height: 1.7;
        font-size: 17px;
        color: var(--text-color);
        margin-bottom: 33px;
      }
    }
  }

  .post__navigation {
    border-bottom: 1px solid var(--mode-border);
    border-top: 1px solid var(--mode-border);
    padding: 20px 0;

    .social {
      display: flex;
      justify-content: space-between;

      .likes {
        color: var(--text-color);
      }

      .icon {
        margin-left: 16px;
        color: var(--text-color);
      }
    }

    .prev-next {
      display: flex;
      justify-content: space-between;
      padding-top: 27px;

      & > div {
        display: flex;
      }

      .prev-next__info {
        display: flex;
        flex-wrap: wrap;
        box-sizing: border-box;
        padding-left: 17px;
        align-content: center;
        font-size: 17px;
        text-transform: capitalize;

        .title {
          width: 100%;
          padding-bottom: 14px;
          color: var(--title-color);
        }

        .name {
          color: var(--text-color);
        }
      }

      .next__info {
        text-align: right;
        padding-right: 13px;

        .name {
          width: 100%;
        }
      }
    }
  }

  .comments {
    .comments-count {
      padding: 18px 0;
      font-size: 21px;
      font-weight: 600;
      margin-bottom: 19px;
      color: var(--title-color);

      span {
        color: var(--main-color);
      }
    }

    .comment {
      display: flex;
      margin-bottom: 50px;

      .comment__img {
        width: 10%;

        img {
          width: 100%;
        }
      }

      .comment__info {
        width: 90%;
        box-sizing: border-box;
        padding-left: 23px;
        padding-right: 23px;

        p {
          margin: 0;
          margin-bottom: 15px;
          line-height: 1.6;
          color: var(--text-color);
        }

        .date {
          width: max-content;
          margin: auto;
          color: var(--text-color);
        }
      }
    }
  }

  form {
    width: 100%;
  }
}
@media #{$information-desktops} {
  .postDetails {
    width: 100%;
    padding: 35px 0;
    .post {
      .post__info {
        padding: 16px 24px;
        padding-left: 0;

        p {
          width: 95%;
          line-height: 1.5;
          font-size: 15px;
          color: #555;
          margin-bottom: 14px;
        }
      }
    }
    .post__navigation {
      padding: 10px 0;

      .social {
        font-size: 13px;

        .icon {
          margin-left: 10px;
        }
      }

      .prev-next {
        padding-top: 11px;

        .prev-next__info {
          font-size: 13px;

          .title {
            padding-bottom: 6px;
          }
        }

        .next__info {
          text-align: right;
          padding-right: 13px;

          .name {
            width: 100%;
          }
        }
      }
    }

    .comments {
      .comments-count {
        font-size: 18px;
        margin-bottom: 6px;

        span {
          color: var(--main-color);
        }
      }

      .comment {
        padding-bottom: 10px;
        margin-bottom: 10px;
        border-bottom: 1px solid var(--mode-border);

        .comment__img {
          width: 10%;

          img {
            width: 100%;
          }
        }

        .comment__info {
          p {
            margin-bottom: 8px;
            font-size: 15px;
            line-height: 1.5;
            color: #555;
          }

          .date {
            width: max-content;
            margin: auto;
            color: #777;
            font-size: 12px;
          }
        }
      }
    }

    form {
      width: 100%;
    }
  }
}
