@media #{$information-phone} {
  .postDetails {
    width: 100%;
    padding: 35px 0;
    .post {
      .post__info {
        padding: 16px 0px;
        p {
          width: 100%;
          line-height: 1.5;
          font-size: 13px;
          margin-bottom: 7px;
          text-align: justify;
        }
      }
    }

    .post__navigation {
      padding: 10px 0;

      .social {
        font-size: 11px;

        .icon {
          margin-left: 10px;
        }
      }

      .prev-next {
        padding-top: 11px;

        .prev-next__img {
          max-width: 50px;

          img {
            width: 100%;
          }
        }

        .prev-next__info {
          font-size: 11px;
          padding-left: 10px;

          .title {
            padding-bottom: 6px;
          }

          .name {
            display: none;
          }
        }

        .next__info {
          text-align: right;
          padding-right: 10px;
        }
      }
    }

    .comments {
      .comments-count {
        font-size: 16px;
        margin-bottom: 6px;
      }

      .comment {
        padding-bottom: 10px;
        margin-bottom: 10px;
        border-bottom: 1px solid var(--mode-border);

        .comment__img {
          width: 10%;

          img {
            width: 100%;
          }
        }

        .comment__info {
          width: 100%;
          padding-right: 8px;

          p {
            font-size: 12px;
            margin-bottom: 7px;
          }

          .date {
            font-size: 9px;
          }
        }
      }
    }

    form {
      width: 100%;
    }
  }
}
