.arrivals {
  padding: 60px 0;
  .arrivals__title {
    padding: 20px 0;
    h2 {
      text-transform: capitalize;
      font-size: 50px;
      color: var(--title-color);
    }
  }
  .owl-nav {
    display: block !important;
    position: absolute;
    top: -120px;
    right: 0;
    color: var(--main-color);
    font-size: 66px;
    button {
      outline: none;
      margin-left: 25px;
    }
  }
  .arrivals__row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    .product--col {
      width: 98%;
      margin: 3px 1%;
    }
  }
}
@media #{$information-large-desktops} {
  .arrivals {
    padding: 60px 0;
    .arrivals__title {
      padding: 20px 0;
      h2 {
        text-transform: capitalize;
        font-size: 40px;
        color: var(--title-color);
      }
    }
    .owl-nav {
      display: block !important;
      position: absolute;
      top: -100px;
      right: 0;
      color: var(--main-color);
      font-size: 55px;
      button {
        outline: none;
        margin-left: 25px;
      }
    }
    .arrivals__row {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
      .product--col {
        width: 98%;
        margin: 3px 1%;
      }
    }
  }
}
@media #{$information-desktops} {
  .arrivals {
    padding: 45px 0;
    .arrivals__title {
      padding: 20px 0;
      h2 {
        font-size: 38px;
      }
    }
    .owl-nav {
      top: -100px;

      font-size: 50px;
      button {
        margin-left: 28px;
      }
    }
  }
}
@media #{$information-tablet} {
  .arrivals {
    padding: 35px 0;

    .arrivals__title {
      padding: 20px 0;

      h2 {
        font-size: 30px;
      }
    }
    .owl-nav {
      top: -87px;

      font-size: 55px;
      button {
        outline: none;
        margin-left: 25px;
      }
    }
    .arrivals__row {
      flex-wrap: wrap;
    }
  }
}
@media #{$information-phone} {
  .arrivals {
    padding: 35px 0;
    padding-bottom: 0;
    .arrivals__title {
      padding: 20px 0;
      display: flex;
      justify-content: space-between;
      h2 {
        font-size: 23px;
        margin: 10px 0;
      }
    }
    .owl-nav {
      font-size: 40px;
      display: block !important;
      color: var(--main-color);
      position: absolute;
      top: -66px;
      right: 0;
      button {
        outline: none;
        margin-left: 17px;
      }
    }
  }
}
