.up {
  color: #fff;
  z-index: 95;
  bottom: 55px;
  background: var(--main-color);
  width: 55px;
  height: 55px;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  font-size: 19px;
  right: 55px;
  display: none;
  cursor: pointer;
}

@media #{$information-tablet} {
  .up {
    bottom: 30px;
    width: 35px;
    height: 35px;
    font-size: 13px;
    right: 30px;
  }
}

@media #{$information-phone} {
  .up {
    bottom: 25px;
    width: 30px;
    height: 30px;
    font-size: 12px;
    right: 25px;
  }
}
