:root {
  --main-color: #ff0000;
  --title-color: #111;
  --text-color: #666;
  --mode-background: #fff;
  --mode-border: rgba(0, 0, 0, 0.1);
}

body {
  background-color: var(--mode-background);
  padding-top: 10vh;
  font-family: 'Roboto', sans-serif !important;
  overflow: visible !important;
}
h1,
h2,
h3,
h4,
.h1,
.h2,
.h3,
.text-title {
  font-family: 'Montserrat', sans-serif !important;
  text-transform: capitalize;
}
.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: auto;
}
.suc {
  position: absolute;
  bottom: 0;
  background: var(--main-color);
  color: #fff;
  width: 37%;
  font-size: 11px;
  font-weight: 600;
  padding: 8px;
  box-sizing: border-box;
  text-transform: capitalize;
}
.model {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  height: 100%;
  width: 100%;
  display: none;
}
.modal {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
}
.shop__container {
  max-width: 1600px;
  padding: 0 30px;
  margin: auto;
}

@media #{$information-desktops} {
  body {
    padding-top: 73px;
  }

  .container {
    max-width: auto;
    width: 90%;
  }
}
@media #{$information-tablet} {
  body {
    padding-top: 73px;
  }

  .container {
    max-width: auto;
    width: 85%;
  }
}

@media #{$information-phone} {
  body {
    padding-top: 69px;
  }

  .container {
    max-width: auto;
    width: 90%;
  }
  .shop__container {
    max-width: 100%;
    padding: 0 15px;
  }
}
