.filter--container {
  width: 24%;
  padding-right: 11px;
  box-sizing: border-box;
  .filter__head {
    display: none;
  }
  .filter {
    & > section {
      border: 1px solid var(--mode-border);
      padding: 8px 15px;
      border-radius: 9px;
      margin-bottom: 30px;
      .head {
        padding: 10px 0;
        margin-bottom: 10px;
        border-bottom: 1px solid var(--mode-border);
        h3 {
          font-size: 22px;
          text-transform: capitalize;
          margin: 0;
          color: var(--title-color);
        }
        span {
          display: none;
        }
      }
      form {
        height: auto;
        overflow: inherit;
        visibility: visible;
        padding-top: 11px;
        & > ul {
          padding: 0;
          margin: 0;
          list-style: none;
          & > li {
            margin-bottom: 20px;
            display: flex;
            justify-content: space-between;
            .input_filter {
              align-items: center;
              display: flex;
              width: 85%;
            }
            span {
              font-size: 15px;
              color: var(--text-color);
            }
            input {
              margin-right: 10px;
              cursor: pointer;
            }
            .input-wrapper {
              position: relative;
              padding-left: 25px;
              display: flex;
              align-items: center;
              justify-content: flex-start;
              min-width: 175px;
              .input {
                display: none;
              }
              label {
                font-size: 15px;
                text-transform: capitalize;
                color: var(--text-color);
                letter-spacing: 1px;
                cursor: pointer;
              }
              .input-label {
                width: 18px;
                height: 18px;
                position: absolute;
                top: 50%;
                transition: All 0.2s ease-in-out;
                left: 0;
                background: #fff;
                border: 1px solid var(--mode-border);
                color: #fff;
                border-radius: 3px;
                font-size: 14px;
                transform: translateY(-50%);
                margin-bottom: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                &::after {
                  content: '\f00c';
                  font-family: 'Font Awesome 5 Free';
                  font-weight: 300;
                  color: #fff;
                  font-size: 10px;
                }
              }
              .input:checked ~ .input-label {
                background: var(--main-color);
                border: 1px solid var(--main-color);
              }
            }
          }
        }
      }
      &.active {
        form {
          height: 0px;
          overflow: hidden;
          visibility: hidden;
        }
      }
    }
    .price {
      form {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        span {
          font-size: 32px;
        }
        input {
          width: 39%;
          border: 1px solid var(--mode-border);
          padding: 9px;
          box-sizing: border-box;
          outline: none;
          border-radius: 5px;
          font-size: 15px;
        }
      }
    }
  }
}
@media #{$information-large-desktops} {
  .filter--container {
    width: 80%;
    display: none;
    position: fixed;
    background: #fff;
    z-index: 666;
    left: 50%;
    padding-right: 0;
    top: 50%;
    transform: translate(-50%, -50%);
    height: 95%;
    overflow: hidden;
    .filter {
      padding-top: 54px;
      height: 90%;
      overflow: auto;
      background: var(--mode-background);
      & > section {
        border: none;
        margin-bottom: 0;
        .head {
          h3 {
            font-size: 17px;
          }
          .icon-filter-bar {
            font-size: 15px;
          }
        }
        form {
          li {
            margin-bottom: 11px;
            .input-wrapper {
              position: relative;
              padding-left: 36px;
              display: flex;
              align-items: center;
              justify-content: flex-start;
              min-width: 175px;
              .input {
                display: none;
              }
              label {
                font-size: 15px;
                text-transform: capitalize;
                color: var(--text-color);
                letter-spacing: 1px;
              }
            }
          }
        }
      }
      & .active {
        form {
          height: 0px;
          overflow: hidden;
          visibility: hidden;
        }
      }
      .price {
        form {
          align-items: center;
          span {
            font-size: 18px;
          }
          input {
            font-size: 11px;
          }
        }
      }
    }
    .filter__head {
      position: fixed;
      width: 100%;
      top: 0;
      padding: 14px 15px;
      left: 0;
      border-radius: 0;
      right: 0;
      z-index: 111;
      box-sizing: border-box;
      color: var(--main-color);
      display: flex;
      background: var(--mode-background);
      border-bottom: 1px solid var(--mode-border);
      .filter__title {
        width: 50%;
        text-transform: capitalize;
        font-size: 24px;
        font-weight: 700;
      }
      .filter__close {
        display: flex;
        width: 50%;
        align-items: center;
        justify-content: flex-end;
      }
    }
  }
}

