.overlay--loading {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--mode-background);
  z-index: 999999;
  overflow: hidden;

  .overlay--loading__info {
    width: 120px;
    height: 120px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    .overlay--loading__border {
      border: 3px solid;
      border-radius: 50%;
      border-color: #fff;
      border-top-color: var(--main-color);
      background-color: transparent;
      position: absolute;
      box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
      height: 100%;
      width: 100%;
      transform: rotate(360deg);
      animation-name: border;
      animation-iteration-count: infinite;
      animation-duration: 1s;
      animation-timing-function: linear;

      &::before {
        content: '';
        background: var(--main-color);
        width: 20px;
        height: 3px;
      }
    }

    @keyframes border {
      from {
        transform: rotate(0deg);
      }

      to {
        transform: rotate(360deg);
      }
    }

    .overlay--loading__logo {
      height: 100%;
      width: 100%;
      background-color: #fff;
      border: 3px solid #fff;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #111;
      font-weight: 600;

      span {
        color: var(--main-color);
        margin-left: 3px;
      }
    }
  }
}

@media #{$information-tablet} {
  .overlay--loading {
    .overlay--loading__info {
      width: 90px;
      height: 90px;

      .overlay__logo {
        font-size: 11px;
      }
    }
  }
}

@media #{$information-phone} {
  .overlay--loading {
    .overlay--loading__info {
      width: 75px;
      height: 75px;

      .overlay--loading__logo {
        font-size: 10px;
      }
    }
  }
}
