.section-products {
  padding: 60px 0;
  .content__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 40px 0;
  }
}

@import './filter/filter';
@import './filter/select';
@import './products/products';

@media #{$information-tablet} {
  .section-products {
    padding: 35px 0;
    .content__row {
      margin: 5px 0;
    }
  }
}
@media #{$information-phone} {
  .section-products {
    padding: 30px 0;
    .content__row {
      margin: 5px 0;
    }
  }
}
