.gallery {
  padding: 60px 0;

  .gallery__container {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;

    img {
      width: 100%;
      height: 100%;
    }

    .left--img {
      width: 49%;
      height: 622px;
    }

    .right--img {
      width: 49%;
      display: flex;
      justify-content: space-between;
      height: 622px;

      .right__left-img {
        width: 48%;
      }

      .right__right-img {
        width: 48%;
        display: flex;
        flex-wrap: wrap;
        align-content: space-between;

        .right__top-img {
          height: 48%;
        }

        .right__bottom-img {
          height: 48%;
        }
      }
    }
  }
}
@media #{$information-large-desktops} {
  .gallery {
    padding: 60px 0;

    .gallery__container {
      display: flex;
      flex-wrap: nowrap;
      justify-content: space-between;

      img {
        width: 100%;
        height: 100%;
      }

      .left--img {
        width: 49%;
        height: auto;
      }

      .right--img {
        width: 49%;
        display: flex;
        justify-content: space-between;
        height: auto;

        .right__left-img {
          width: 48%;
        }

        .right__right-img {
          width: 48%;
          display: flex;
          flex-wrap: wrap;
          align-content: space-between;

          .right__top-img {
            height: 48%;
          }

          .right__bottom-img {
            height: 48%;
          }
        }
      }
    }
  }
}
@media #{$information-desktops} {
  .gallery {
    padding: 60px 0;
    .gallery__container {
      display: flex;
      flex-wrap: nowrap;
      justify-content: space-between;
      img {
        width: 100%;
        height: 100%;
      }
      .left--img {
        width: 49%;
        height: auto;
      }
      .right--img {
        width: 49%;
        display: flex;
        justify-content: space-between;
        height: auto;
        .right__left-img {
          width: 48%;
        }
        .right__right-img {
          width: 48%;
          display: flex;
          flex-wrap: wrap;
          align-content: space-between;
          .right__top-img {
            height: 48%;
          }
          .right__bottom-img {
            height: 48%;
          }
        }
      }
    }
  }
}
@media #{$information-tablet} {
  .gallery {
    padding: 35px 0;
    overflow: hidden;

    .gallery__container {
      flex-wrap: wrap;

      .left--img {
        width: 100%;
        height: auto;
        margin-bottom: 12px;
      }

      .right--img {
        width: 100%;
        height: auto;

        .right__left-img {
          width: 49%;
          height: auto;
          margin-bottom: 0;
        }

        .right__right-img {
          width: 49%;

          & > div {
            width: 100%;
          }
        }
      }
    }
  }
}
@media #{$information-phone} {
  .gallery {
    padding: 35px 0;
    width: 100%;
    overflow: hidden;

    .gallery__container {
      flex-wrap: wrap;

      img {
        width: auto;
        height: auto;
      }

      .left--img {
        width: 100%;
        height: auto;
        margin-bottom: 18px;
      }

      .right--img {
        width: 100%;
        flex-wrap: wrap;
        height: auto;

        .right__left-img {
          width: 100%;
          height: auto;
          margin-bottom: 18px;
        }

        .right__right-img {
          width: 100%;

          & > div {
            width: 100%;
          }
        }
      }
    }
  }
}
