.cart {
  padding-top: 60px;
  .head_products {
    display: flex;
    text-transform: capitalize;
    padding: 30px 0;
    color: var(--title-color);
    font-size: 25px;
    border-bottom: 1px solid var(--mode-border);
    margin-bottom: 36px;
    & > div {
      width: 20%;
      text-align: center;
      &.name--product {
        width: 40%;
        text-align: left;
      }
    }
  }
  .products {
    padding-bottom: 20px;
    border-bottom: 1px solid var(--mode-border);
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    .empty {
      color: var(--text-color);
      font-size: 20px;
    }
    .productsList {
      width: 100%;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
    }
    .product {
      position: relative;
      width: 100%;
      padding: 10px;
      box-sizing: border-box;
      margin-bottom: 15px;
      box-shadow: 0 0 5px 0px var(--mode-border);
      border-radius: 5px;
      .close {
        position: absolute;
        font-size: 25px;
        top: 10px;
        right: 10px;
        color: var(--title-color);
        &::before {
          content: '';
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          display: block;
        }
      }
      .content {
        overflow: hidden;
        width: 100%;
        display: flex;
        .content_img {
          width: 15%;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .content_info {
          width: 85%;
          padding-left: 12px;
          display: flex;
          align-items: center;
          & > div,
          form {
            height: max-content;
          }
          .total--price {
            text-align: center;
            width: 21%;
            span {
              letter-spacing: 1px;
              color: var(--main-color);
              font-size: 18px;
              margin-top: 4px;
            }
          }
          .color {
            display: none;
          }
          .progress-stars {
            display: none;
          }
          .name {
            width: 31%;
            color: var(--title-color);
            h3 {
              margin: 0;
              text-transform: capitalize;
              font-weight: 300;
              font-size: 21px;
              a {
                text-decoration: none;
                color: var(--title-color);
                &:hover {
                  color: var(--main-color);
                }
              }
            }
          }
          .price {
            color: var(--main-color);
            font-size: 18px;
            margin-top: 4px;
            text-align: center;
            width: 20%;
            letter-spacing: 1px;
          }
          .count-product {
            width: 28%;
            display: flex;
            justify-content: flex-end;
            & > div {
              width: 120px;
              margin: auto;
              position: relative;

              & > div {
                width: 31px;
                font-size: 9px;
                position: absolute;
                background: var(--main-color);
                display: flex;
                color: #fff;
                align-items: center;
                justify-content: center;
                top: 0;
                height: 100%;
              }
              .plus {
                right: 0;
              }
              .minus {
                left: 0;
              }
            }
            .input-count {
              width: 100%;
              border: 1px solid var(--mode-border);
              font-size: 12px;
              padding: 10px;
              box-sizing: border-box;
              outline-color: var(--main-color);
              text-align: center;
            }
          }
        }
      }
    }
  }
  .cart__update,
  .shoping {
    padding: 30px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid var(--mode-border);
    button {
      border: none;
      padding: 16px 32px;
      background-color: var(--main-color);
      color: #fff;
      font-weight: 700;
      outline: none;
      font-size: 15px;
      text-transform: capitalize;
    }
  }
  .cart__total {
    padding: 20px 0;
    display: flex;
    justify-content: flex-end;
    td {
      font-size: 18px;
      text-transform: capitalize;
      font-weight: 400;
      letter-spacing: 2px;
      color: var(--title-color);
      padding-top: 11px;
      &.total__number {
        padding-left: 20px;
      }
    }
  }
  .shoping {
    justify-content: flex-end;
  }
}
@media #{$information-desktops} {
  .cart {
    .head_products {
      padding: 20px 0;
      font-size: 18px;
      margin-bottom: 30px;
    }
    .products {
      .empty {
        font-size: 16px;
      }
      .product {
        .close {
          font-size: 16px;
        }
        .content {
          overflow: hidden;
          width: 100%;
          display: flex;
          .content_info {
            .total--price {
              span {
                font-size: 13px;
              }
            }
            .color {
              display: none;
            }
            .progress-stars {
              display: none;
            }
            .name {
              width: 31%;
              color: var(--title-color);
              h3 {
                margin: 0;
                text-transform: capitalize;
                font-weight: 400;
                font-size: 15px;
              }
            }
            .price {
              color: var(--main-color);
              font-size: 13px;
              margin-top: 4px;
              text-align: center;
              width: 20%;
              letter-spacing: 1px;
            }
            .count-product {
              & > div {
                width: 79px;
                margin: auto;
                position: relative;

                & > div {
                  width: 23px;
                }
              }
              .input-count {
                padding: 4px;
              }
            }
          }
        }
      }
    }
    .cart__update,
    .shoping {
      padding: 30px 0;
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid var(--mode-border);
      button {
        outline: none;
        font-size: 13px;
      }
    }
    .cart__total {
      td {
        font-size: 15px;
      }
    }
  }
}
