.about {
  padding: 60px 0;
  .about__content {
    max-width: 100%;
    .container--360deg {
      position: relative;
      width: 100%;
      height: 600px;
      .overlay_360deg {
        position: absolute;
        top: -1px;
        left: -1px;
        height: 60px;
        width: 60px;
        background: #fff;
        display: flex;
        align-items: flex-start;
        justify-content: flex-start;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .pano-image {
        width: 100%;
        height: 100%;
        .panolens-canvas {
          width: 100% !important;
          height: 100% !important;
          display: block !important;
        }
        div {
          span {
            &:nth-child(3),
            &:nth-child(2) {
              width: 50px !important;
            }
          }
        }
      }
    }
    h2 {
      font-size: 29px;
      position: relative;
      display: flex;
      padding-left: 75px;
      color: var(--title-color);

      &::before {
        content: '';
        position: absolute;
        top: 44%;
        left: 0;
        background: var(--main-color);
        height: 3px;
        width: 60px;
        transform: translateY(-50%);
      }
    }

    p {
      font-size: 16px;
      line-height: 1.6;
      width: 90%;
      color: var(--text-color);
    }

    .Our-Mission {
      padding: 45px 0;
    }
  }
}
@media #{$information-large-desktops} {
  .about {
    .about__content {
      .container--360deg {
        height: 500px;
      }
    }
  }
}
@media #{$information-desktops} {
  .about {
    .about__content {
      .container--360deg {
        height: 350px;
        .overlay_360deg {
          height: 50px;
          width: 50px;
        }
      }
      h2 {
        padding-left: 60px;

        &::before {
          content: '';
          position: absolute;
          top: 44%;
          left: 0;
          background: var(--main-color);
          height: 3px;
          width: 46px;
          transform: translateY(-50%);
        }
      }

      p {
        font-size: 15px;
        line-height: 1.5;
        width: 97%;
        color: var(--text-color);
      }

      .Our-Mission {
        padding: 45px 0;
      }
    }
  }
}
