footer {
  padding: 60px 0;

  .footer__content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: var(--title-color);

    .col {
      width: 20%;
      text-align: left;
      h2 {
        text-transform: capitalize;
        font-size: 30px;
        margin: 0;
        margin-bottom: 27px;
        cursor: pointer;
        span {
          margin-left: 5px;
          text-transform: capitalize;
          display: inline-block;
          color: var(--main-color);
        }
      }

      p {
        font-size: 17px;
        line-height: 1.5;
        color: var(--text-color);
      }

      h3 {
        font-size: 21px;
        margin: 0;
        margin-bottom: 30px;
      }

      ul {
        list-style: none;
        padding: 0;
        margin: 0;

        li {
          margin-bottom: 17px;

          a {
            text-decoration: none;
            font-size: 17px;
            color: var(--text-color);
          }
        }
      }

      &:first-child {
        width: 22%;
      }
    }
  }
}

.copyRight {
  padding-bottom: 70px;

  .copyright-content {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;

    .info {
      font-size: 16px;
      text-transform: capitalize;
      color: var(--text-color);

      .aothur {
        color: var(--main-color);
      }
    }
  }

  .social {
    .icon {
      margin-left: 16px;
      color: var(--text-color);
      cursor: pointer;
      &:hover {
        color: var(--main-color);
      }
    }
  }
}
@media #{$information-large-desktops} {
  footer {
    .footer__content {
      .col {
        p {
          font-size: 15px;
        }
        ul {
          li {
            a {
              font-size: 15px;
            }
          }
        }
      }
    }
  }
}

@media #{$information-desktops} {
  footer {
    padding: 45px 0;
    .footer__content {
      flex-wrap: wrap;
      .col {
        width: 27%;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        margin-bottom: 14px;
        border-bottom: none;
        h2 {
          font-size: 27px;
          margin-bottom: 10;
        }

        p {
          font-size: 14px;
          line-height: 1.5;
        }

        h3 {
          font-size: 20px;
          margin-bottom: 25px;
        }

        ul {
          li {
            margin-bottom: 14px;

            a {
              font-size: 14px;
            }
          }
        }

        &:first-child {
          width: 40%;
        }
      }
    }
  }

  .copyRight {
    .copyright-content {
      justify-content: flex-start;
      flex-wrap: wrap;
      padding: 0;
      .info {
        width: 80%;
        font-size: 12px;
        line-height: 1.6;
      }
    }

    .social {
      width: 20%;
      .icon {
        margin-left: 13px;
        font-size: 12px;
        &:first-child {
          margin-left: 0;
        }
      }
    }
  }
}

