        }

        h3 {
          font-size: 21px;
          margin-bottom: 14px;
        }

        ul {
          li {
            margin-bottom: 10px;

            a {
              font-size: 14px;
            }
          }
        }

        &:first-child {
          width: 100%;
        }
      }
    }
  }

  .copyRight {
    .copyright-content {
      justify-content: flex-start;
      flex-wrap: wrap;
      padding: 0;

      .info {
