  .copyRight {
    .copyright-content {
      justify-content: flex-start;
      flex-wrap: wrap;
      padding: 0;

      .info {
        font-size: 12px;
        line-height: 1.6;
      }
    }

    .social {
      margin-top: 10px;

      .icon {
