.get-in-touch__content {
  display: flex;
}
.form--commet {
  width: 66%;
  textarea,
  input {
    width: 100%;
    border: 1px solid var(--mode-border);
    padding: 15px;
    box-sizing: border-box;
    background: var(--mode-background);
    outline: none;
    color: var(--text-color);
    margin-bottom: 24px;
  }
  textarea {
    resize: none;
    height: 185px;
  }
  .email-name {
    display: flex;
    justify-content: space-between;
    input {
      width: 48%;
    }
  }
  ::placeholder {
    color: var(--text-color);
    text-transform: capitalize;
    font-size: 14px;
  }
  button {
    border: none;
    padding: 15px 32px;
    color: #fff;
    background-color: var(--main-color);
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 3px;
    outline: none;
    font-size: 14px;
  }
}
@media #{$information-desktops} {
  .form--commet {
    width: 100%;
    textarea,
    input {
      width: 100%;
      padding: 12px;
      margin-bottom: 10px;
    }
    textarea {
      resize: none;
      height: 170px;
    }
    .email-name {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      input {
        width: 48%;
      }
    }
    ::placeholder {
      color: rgba(0, 0, 0, 0.6);
      text-transform: capitalize;
      font-size: 11px;
    }
  }
}
@media #{$information-tablet} {
  .form--commet {
    width: 100%;
    textarea,
    input {
      width: 100%;
      padding: 12px;
      margin-bottom: 10px;
    }
    textarea {
      resize: none;
      height: 170px;
    }
    .email-name {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      input {
        width: 100%;
      }
    }
    ::placeholder {
      color: rgba(0, 0, 0, 0.6);
      text-transform: capitalize;
      font-size: 11px;
    }
    button {
      font-size: 11px;
    }
  }
}
@media #{$information-phone} {
  .form--commet {
    width: 100%;
    textarea,
    input {
      width: 100%;
      padding: 13px;
      margin-bottom: 10px;
      font-size: 13px;
      color: var(--text-color);
      border: 1px solid var(--mode-border);
      background: var(--mode-background);
    }
    textarea {
      resize: none;
      height: 140px;
    }
    .email-name {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      input {
        width: 100%;
      }
    }
    ::placeholder {
      color: var(--text-color);
      text-transform: capitalize;
      font-size: 11px;
    }
    button {
      font-size: 11px;
    }
  }
}
