.global--filter {
  display: none;
}
.products {
  width: 76%;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  .sort-icon {
    display: none;
  }
  .products__sort {
    width: 100%;
    .Arrangement {
      .search {
        margin-bottom: 15px;
        form {
          position: relative;
          .search__input {
            width: 100%;
            box-sizing: border-box;
            padding: 17px;
            border: 1px solid var(--mode-border);
            border-radius: 7px;
            background: var(--mode-background);
            outline: none;
            color: var(--text-color);
          }
          ::placeholder {
            color: var(--text-color);

            text-transform: capitalize;
            font-size: 13px;
          }
          .search__icon {
            border: none;
            background: transparent;
            position: absolute;
            right: 18px;
            font-size: 18px;
            color: var(--main-color);
            top: 50%;
            transform: translateY(-50%);
            outline: none;
          }
        }
      }
      .filter__parts {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .icons_part {
          display: flex;
          .part--icon {
            font-size: 18px;
            margin-left: 22px;
            color: var(--text-color);
            cursor: pointer;
            text-transform: capitalize;
            display: inherit;
            justify-content: center;
            width: 128px;
            box-sizing: border-box;
            border-radius: 6px;
            padding: 13px 0;
            border: 1px solid var(--mode-border);
          }
        }
      }
    }
  }
  .products__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding: 50px 0;
    align-content: flex-start;
    .productsBoxs {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      min-height: 1940px;
      justify-content: flex-start;
      align-content: flex-start;
    }
    .product--col {
      .col__img {
        height: 290px !important;
      }
      &.details {
        width: 100%;
        margin: 15px 0;
        padding: 0;
        overflow: hidden;
        border: 1px solid var(--mode-border);
        text-align: left;
        .col__box {
          display: flex;
        }
        .col__img {
          width: 34%;
          img {
            width: 100%;
          }
        }
        .col__info {
          width: 66%;
          padding: 23px 15px;
          display: flex;
          height: auto;
          flex-wrap: wrap;
          box-sizing: border-box;
          align-content: space-between;
          .info {
            overflow: auto;
          }
          .head {
            display: flex;
            align-items: center;
            justify-content: space-between;
            .price {
              font-size: 20px;
              font-weight: 700;
              color: var(--main-color);
              letter-spacing: 1px;
            }
          }
          table {
            margin-top: 15px;
            width: 100%;
            td {
              padding: 3px 0;
            }
          }
          .desc__span {
            display: block;
            font-size: 20px;

            color: var(--title-color);
          }
          span {
            font-size: 15px;
            text-transform: capitalize;

            color: var(--title-color);
          }
          h3 {
            font-size: 24px;
            text-transform: capitalize;
            margin: 0;
            font-weight: 100;
            color: var(--title-color);
            .title_span {
              font-size: 24px;
              text-transform: capitalize;
              font-weight: 600;
              color: var(--title-color);
            }
          }
          p {
            font-size: 15px;
            line-height: 1.5;
            margin: 0;
            color: var(--text-color);
          }
          .desc {
            margin: 10px 0;
          }
          .cart {
            padding-top: 20px;
          }
          button {
            border: none;
            text-transform: capitalize;
            background-color: var(--main-color);
            color: #fff;
            padding: 14px 32px;
            font-weight: 700;
            font-size: 15px;

            outline: none;
            letter-spacing: 1px;
          }
        }
      }
    }
  }
  .products__navigation {
    width: 406px;
    margin: auto;
    height: 38px;
    padding: 3px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .pagination_icon {
      width: 20%;
      align-items: center;
      height: 100%;
      font-size: 22px;
      border: 1px solid var(--mode-border);
      color: var(--text-color);
      display: flex;
      justify-content: center;
    }
    .navs {
      display: none;
    }
    .navs-mobile {
      width: 50%;
      height: 100%;
      display: flex;
      align-items: center;
      font-size: 12px;
      text-transform: capitalize;
      div {
        width: 33%;
        height: 100%;
        color: var(--text-color);
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 26px;
        font-weight: 600;
        .slash {
          font-weight: 700;
          transform: rotateZ(81deg);
        }
      }
      .number-page--start {
        padding-right: 14px;
        input {
          width: 81px;
          height: 100%;
          box-sizing: border-box;
          padding: 4px 4px;
          text-align: center;
          border: 1px solid var(--mode-border);
          color: #333;
          outline: none;
        }
      }
    }
  }
}
