.product--col {
  text-align: center;
  padding: 16px 19px;
  text-align: center;
  width: 33.3%;
  border-radius: 20px;
  box-sizing: border-box;
  .watch__img {
    position: relative;
    overflow: hidden;
    height: 355px;
    font-size: 30px;
    img {
      width: 100%;
      height: 100%;
    }
    ul {
      position: absolute;
      top: 10px;
      list-style: none;
      padding: 0;
      margin: 0;
      right: 3px;
      font-size: 13px;
      display: none;
      li {
        margin-top: 9px;
        position: relative;
        border-radius: 50%;
        cursor: pointer;
        overflow: hidden;
        box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.1);
        .watch-click {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          cursor: pointer;
          background: transparent;
        }
        .icon {
          background: #fff;
          width: 42px;
          display: flex;
          height: 42px;
          justify-content: center;
          align-items: center;
          cursor: pointer;
          color: var(--main-color);
        }
      }
    }
  }
  .watch__info {
    padding: 14px 0;
    padding-bottom: 0;

    .model--price {
      display: flex;
      justify-content: space-between;
      align-items: center;
      h3 {
        font-size: 16px;
        margin-top: 0px;
        margin-bottom: 0px;
        font-weight: 400;
        a {
          text-decoration: none;
          color: var(--title-color);
          &:hover {
            color: var(--main-color);
          }
        }
      }
      .price {
        font-size: 16px;
        font-weight: 700;
        color: var(--main-color);
        letter-spacing: 1px;
      }
    }
    .brand--color {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 7px 0;
      font-size: 15px;
      color: var(--text-color);
      text-transform: capitalize;
      font-weight: 400;
      h4 {
        font-weight: 100;
        margin: 0;
      }
    }
  }
  &:hover {
    box-shadow: 0 0 4px 0 var(--mode-border);
    .watch__img {
      ul {
        display: block;
      }
    }
  }
}
