.testmonials {
  border-top: 1px solid var(--mode-border);
  border-bottom: 1px solid var(--mode-border);
  .testmonial {
    overflow: hidden;
    margin: 7px 0;
    display: flex;
    flex-wrap: wrap;
    .testmonial_desc {
      color: #fff;
      padding: 20px 0;
      margin: auto;
      padding-bottom: 50px;
      .head {
        text-transform: capitalize;
        text-align: center;
        padding: 0px;
        font-size: 24px;
        h3 {
          margin: 12px;
          color: var(--main-color);
        }
      }
      .testmonial_containe {
        width: 63%;
        margin: auto;
        p {
          margin: 30px;
          font-size: 17px;
          color: var(--text-color);
          text-align: center;
          line-height: 1.9;
        }
      }
    }
    .testmonial_infoUser {
      position: relative;
      width: 100%;
      .image-content {
        width: 75px;
        height: 75px;
        padding: 5px;
        background: var(--mode-background);
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
      }
      .image {
        border-radius: 50%;
        overflow: hidden;
        height: 100%;
        width: 100%;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .rating {
        position: relative;
        width: max-content;
        margin: 55px auto;
        margin-bottom: 11px;
        &::before {
          content: '\f005 \f005 \f005 \f005 \f005';
          font-family: 'Font Awesome 5 Free';
          font-weight: 900;
          color: #ddd;
          font-size: 16px;
        }
        ul {
          list-style: none;
          display: flex;
          width: 100%;
          padding: 0;
          margin: 0;
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          overflow: hidden;
          li {
            .watch__star {
              font-size: 16px;
              color: #ffdf00;
            }
          }
        }
      }
    }
  }
}
@media #{$information-large-desktops} {
  .testmonials {
    .testmonial {
      .testmonial_desc {
        .head {
          font-size: 23px;
        }
        .testmonial_containe {
          width: 65%;
          margin: auto;
          p {
            font-size: 15px;
          }
        }
      }
    }
  }
}
@media #{$information-desktops} {
  .testmonials {
    .testmonial {
      .testmonial_desc {
        .head {
          font-size: 20px;
        }
        .testmonial_containe {
          width: 70%;
          margin: auto;
          p {
            font-size: 15px;
          }
        }
      }
    }
  }
}
@media #{$information-tablet} {
  .testmonials {
    .testmonial {
      .testmonial_desc {
        .head {
          font-size: 18px;
        }
        .testmonial_containe {
          width: 100%;
          margin: auto;
          p {
            font-size: 14px;
            margin: 18px;
          }
        }
      }
    }
  }
}
@media #{$information-phone} {
  .testmonials {
    .testmonial {
      .testmonial_desc {
        .head {
          padding: 0px;
          font-size: 12px;
          h3 {
            margin: 12px;
          }
        }
        .testmonial_containe {
          width: 100%;
          p {
            margin: 0;
            font-size: 12px;
            line-height: 1.5;
          }
        }
      }
      .testmonial_infoUser {
        position: relative;
        width: 100%;
        .image-content {
          width: 60px;
          height: 60px;
          padding: 5px;
          background: var(--mode-background);
          position: absolute;
          top: 0;
          left: 50%;
          transform: translate(-50%, -50%);
          border-radius: 50%;
        }
        .image {
          border-radius: 50%;
          overflow: hidden;
          height: 100%;
          width: 100%;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .rating {
          position: relative;
          width: max-content;
          margin: 38px auto;
          margin-bottom: 11px;
          &::before {
            content: '\f005 \f005 \f005 \f005 \f005';
            font-family: 'Font Awesome 5 Free';
            font-weight: 900;
            color: #ddd;
            font-size: 12px;
          }
          ul {
            list-style: none;
            display: flex;
            width: 100%;
            padding: 0;
            margin: 0;
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            overflow: hidden;
            li {
              .watch__star {
                font-size: 12px;
                color: #ffdf00;
              }
            }
          }
        }
      }
    }
  }
}
