.map {
  padding: 60px 0;

  #map {
    height: 519px;
  }
}
.get-in-touch {
  padding: 60px 0;
  .get-in-touch__content {
    justify-content: space-between;
    flex-wrap: wrap;
    form {
      margin-bottom: 35px;
    }
  }
  h2 {
    font-size: 30px;
    color: var(--title-color);
  }
  .contact_info {
    margin: 0;
    width: max-content;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    li {
      margin-bottom: 25px;
      .info--title {
        font-size: 20px;
        letter-spacing: 1px;
        width: 100%;
        display: block;
        color: var(--title-color);
        margin-bottom: 5px;
      }
      .info--value {
        color: var(--text-color);
        font-size: 18px;
        letter-spacing: 1px;
        padding-left: 15px;
      }
    }
  }
}
@media #{$information-desktops} {
  .map {
    padding: 35px 0;

    #map {
      height: 319px;
    }
  }

  .get-in-touch {
    padding: 0;

    h2 {
      font-size: 23px;
    }
  }
}
@media #{$information-tablet} {
  .map {
    padding: 35px 0;

    #map {
      height: 319px;
    }
  }

  .get-in-touch {
    padding: 0;
    form {
      margin-bottom: 40px;
    }
    .contact_info {
      width: 100%;

      li {
        margin-bottom: 13px;
        .info--title {
          font-size: 17px;
          letter-spacing: 1px;
          width: 100%;
          display: block;
          margin-bottom: 5px;
        }
        .info--value {
          font-size: 13px;
        }
      }
    }
    h2 {
      font-size: 23px;
    }
  }
}

@media #{$information-phone} {
  .map {
    padding: 35px 0;

    #map {
      height: 250px;
    }
  }

  .get-in-touch {
    padding: 0;
    form {
      margin-bottom: 40px;
    }
    .contact_info {
      margin: 0;
      width: 100%;
      padding: 0;
      box-sizing: border-box;
      list-style: none;
      li {
        margin-bottom: 11px;
        .info--title {
          font-size: 15px;
          letter-spacing: 1px;
          width: 100%;
          display: block;
          margin-bottom: 5px;
        }
        .info--value {
          color: var(--text-color);
          font-size: 11px;
          letter-spacing: 1px;
        }
      }
    }
    h2 {
      font-size: 22px;
    }
  }
}
@media #{$information-small--phone} {
  .map {
    #map {
      height: 200px;
    }
  }
}
