.model--authenication {
  z-index: 120;
  &.show {
    display: block;
  }
}
@keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }

  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}
.authenication {
  position: fixed;
  display: none;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 600;
  .authenication_close {
    width: max-content;
    position: fixed;
    top: 15px;
    right: 15px;
    font-size: 24px;
    z-index: 11;
    color: var(--main-color);
  }
  .input-div {
    display: flex;
    border-bottom: 1px solid var(--mode-border);
    width: 80%;
    margin-bottom: 25px;
    transition: all 0.5s ease-in-out;
    .input-icon {
      width: 10%;
      display: flex;
      align-items: center;
      color: rgba(3, 3, 3, 0.4);
    }
    .input {
      width: 90%;
      position: relative;
      .eye-password {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 4px;
        font-size: 14px;
        height: 15px;
        width: 17px;
        color: rgba(3, 3, 3, 0.4);
        .not_eye_icon {
          position: absolute;
          top: 0;
          left: 0;
          z-index: 5;
        }
        .eye_icon {
          position: absolute;
          top: 0;
          left: 0;
          color: #fff;
        }
        .hide {
          display: none;
        }
        .show {
          color: var(--main-color);
        }
      }
      .placeholder {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        color: var(--text-color);
      }
      input {
        background-color: var(--mode-background);
        border: none;
        padding: 16px 0;
        outline: none;
        width: 100%;
        box-sizing: border-box;
        color: var(--title-color);
      }
      .correct,
      .message-error {
        display: none;
      }
    }
    .input-correct {
      .correct {
        height: 14px;
        width: 14px;
        color: #080;
        background: #fff;
        position: absolute;
        top: 50%;
        border-radius: 50%;
        right: 9px;
        transform: translateY(-50%);
        display: block;
      }
      .message-error {
        display: none;
      }
    }
    .input-error {
      .message-error {
        font-size: 12px;
        font-weight: 300;
        transform: translateY(-50%);
        position: absolute;
        width: 177px;
        right: 0;
        top: 0;
        text-transform: capitalize;
        color: #ff0000;
        text-align: end;
        display: block;
      }
      .correct {
        display: none;
      }
    }
    input:-webkit-autofill,
    input:-webkit-autofill:hover,
    input:-webkit-autofill:focus,
    textarea:-webkit-autofill,
    textarea:-webkit-autofill:hover,
    textarea:-webkit-autofill:focus,
    select:-webkit-autofill,
    select:-webkit-autofill:hover,
    select:-webkit-autofill:focus {
      border: none;
      -webkit-text-fill-color: var(--title-color);
      transition: background-color 5000s ease-in-out 0s;
    }
    .input-icon {
      font-size: 13px;
      color: var(--text-color);
    }
  }
  .focus {
    border-bottom: 1px solid var(--main-color);
    .input-icon {
      color: var(--main-color);
    }
    .input {
      .placeholder {
        top: 0;
        transform: translateY(-50%);
      }
    }
  }
}
.authenication__container {
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  width: 950px;
  max-width: 100%;
  min-height: 600px;
  h2 {
    font-weight: bold;
    font-size: 36px;
    margin: 0;
    text-transform: capitalize;
  }
  h3 {
    text-align: center;
  }
  p {
    font-size: 19px;
    font-weight: 100;
    line-height: 1.6;
    letter-spacing: 0.5px;
    margin: 20px 0 30px;
  }
  a {
    color: #333;
    font-size: 14px;
    text-decoration: none;
    margin: 15px 0;
    &:hover {
      color: var(--main-color);
    }
  }
  button {
    border-radius: 20px;
    background: var(--main-color);
    color: #ffffff;
    font-size: 12px;
    width: 180px;
    font-weight: bold;
    padding: 11px 10px;
    margin-top: 5px;
    cursor: pointer;
    letter-spacing: 1px;
    border: none;
    text-transform: uppercase;
    transition: transform 80ms ease-in;
    &:active {
      transform: scale(0.95);
    }
    &:focus {
      outline: none;
    }
  }
  .create-acount,
  .call-sign-in {
    display: none;
  }
  .create-acount,
  .call-sign-in {
    background-color: #fff;
    color: var(--main-color);
    margin-top: 17px;
  }
  form {
    background-color: var(--mode-background);
    color: var(--title-color);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 50px;
    height: 100%;
    text-align: center;
  }
}
.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}
.sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
}
.authenication__container.right-panel-active {
  .sign-in-container {
    transform: translateX(100%);
  }
  .sign-up-container {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
    animation: show 0.6s;
  }
  .overlay-container {
    transform: translateX(-100%);
  }
  .overlay {
    transform: translateX(50%);
  }
  .overlay-left {
    transform: translateX(0);
  }
  .overlay-right {
    transform: translateX(20%);
  }
}
.sign-up-container {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}
.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}
.overlay {
  // background: var(--main-color);
  background-image: url('../img/interface-5.webp');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}
.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  box-sizing: border-box;
  top: 0;
  height: 100%;
  width: 50%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}
.overlay-left {
  transform: translateX(-20%);
}
.overlay-right {
  right: 0;
  transform: translateX(0);
}
.social-container {
  margin: 12px 0;
  margin-bottom: 28px;
  a {
    border: 1px solid var(--mode-border);
    border-radius: 50%;
    display: inline-flex;
    color: var(--text-color);
    justify-content: center;
    align-items: center;
    margin: 0 5px;
    height: 40px;
    width: 40px;
  }
}
