.blog__content {
  display: flex;
  justify-content: space-between;
  padding: 60px 0;
  .posts {
    width: 67%;
  }
  aside {
    width: 30%;
    h3 {
      font-size: 24px;
      margin: 0;
      color: var(--title-color);
      padding-bottom: 14px;
      border-bottom: 1px solid var(--mode-border);
      text-transform: capitalize;
    }
    ul {
      list-style: none;
      padding: 0;
      margin: 0;
    }

    section {
      border: 1px solid var(--mode-border);
      background: var(--mode-background);
      padding: 29px;
      margin-bottom: 31px;
    }

    input {
      width: 100%;
      border: none;
      outline: none;
      padding: 16px;
      margin-bottom: 18px;
      font-size: 13px;
      border: 1px solid var(--mode-border);
      background: var(--mode-background);
      color: var(--text-color);
      box-sizing: border-box;
    }

    ::placeholder {
      color: var(--text-color);

      text-transform: capitalize;
      font-size: 13px;
    }

    button {
      width: 100%;
      border: none;
      background-color: var(--main-color);
      color: #fff;
      padding: 16px;
      text-transform: uppercase;
      font-weight: 700;
      font-size: 14px;
      font-family: 'Josefin Sans', sans-serif !important;
      outline: none;
      letter-spacing: 2px;
    }

    .category {
      ul {
        li {
          padding: 21px 0;
          border-bottom: 1px solid var(--mode-border);
          font-size: 18px;
          color: var(--text-color);

          &:last-child {
            border-bottom: none;
          }
        }
      }
    }

    .recent-post {
      ul {
        li {
          display: flex;
          padding: 13px 0;

          .recent__img {
            width: 24%;

            img {
              height: 100%;
              width: 100%;
            }
          }

          .recent__info {
            width: 76%;
            padding-left: 14px;
            display: flex;
            flex-wrap: wrap;
            align-content: center;
            box-sizing: border-box;

            .recent__title {
              margin-bottom: 8px;
              font-size: 17px;
              white-space: nowrap;
              text-overflow: ellipsis;
              color: var(--title-color);
              overflow: hidden;
            }

            .recent__time {
              color: var(--text-color);
            }
          }
        }
      }
    }

    .tag-clouds {
      .tags__content {
        display: flex;
        flex-wrap: wrap;
        padding: 21px 0;
        padding-bottom: 0;
        padding: 15px 0;

        .tag {
          text-decoration: none;
          color: #333;
          margin: 0 7px;
          background-color: #fff;
          padding: 7px 13px;
          margin-bottom: 15px;
          text-transform: capitalize;
          box-shadow: 0 0 4px 0 var(--mode-border);
        }
      }
    }

    .section-sticky {
      position: sticky;
      top: 140px;
    }

    .instagram-feeds {
      .instagram__content {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        .img {
          width: 31%;

          img {
            width: 100%;
          }
        }
      }
    }

    .sub {
      .sub__content {
        padding: 15px 0;
      }
    }
  }
}
@media #{$information-large-desktops} {
  .blog__content {
    .posts {
      width: 68%;
    }
    aside {
      width: 30%;
      h3 {
        font-size: 22px;
      }
      button {
        font-size: 13px;
      }

      .category {
        ul {
          li {
            font-size: 15px;
          }
        }
      }

      .recent-post {
        ul {
          li {
            display: flex;
            padding: 13px 0;

            .recent__img {
              width: 24%;

              img {
                height: 100%;
                width: 100%;
              }
            }

            .recent__info {
              width: 76%;
              padding-left: 14px;
              display: flex;
              flex-wrap: wrap;
              align-content: center;
              box-sizing: border-box;

              .recent__title {
                margin-bottom: 8px;
                font-size: 15px;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
              }

              .recent__time {
                color: #666;
                font-size: 12px;
              }
            }
          }
        }
      }

      .tag-clouds {
        .tags__content {
          .tag {
            font-size: 15px;
          }
        }
      }
    }
  }
}
@media #{$information-desktops} {
  .blog__content {
    flex-wrap: wrap;
    padding: 35px 0;
    .posts {
      width: 100%;
      padding-bottom: 45px;
    }
    aside {
      width: 100%;
      h3 {
        font-size: 22px;
      }
      button {
        font-size: 13px;
      }

      .category {
        ul {
          li {
            font-size: 15px;
          }
        }
      }

      .recent-post {
        ul {
          li {
            display: flex;
            padding: 13px 0;

            .recent__img {
              width: 24%;

              img {
                height: 100%;
                width: 100%;
              }
            }

            .recent__info {
              width: 76%;
              padding-left: 14px;
              display: flex;
              flex-wrap: wrap;
              align-content: center;
              box-sizing: border-box;

              .recent__title {
                margin-bottom: 8px;
                font-size: 15px;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                width: 100%;
              }

              .recent__time {
                color: #666;
                font-size: 12px;
              }
            }
          }
        }
      }

      .tag-clouds {
        .tags__content {
          .tag {
            font-size: 15px;
          }
        }
      }
      .sub__content,
      .search {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
      }
      input {
        width: 75%;
      }
      button {
        width: auto;
        padding: 16px 32px;
      }
    }
  }
}
@media #{$information-tablet} {
  .blog__content {
    padding: 35px 0;
    flex-wrap: wrap;

    .posts {
      width: 100%;
      padding-bottom: 45px;
    }

    aside {
      width: 100%;
      margin-top: 40px;

      h3 {
        font-size: 19px;
        padding-bottom: 10px;
      }

      section {
        padding: 15px;
        margin-bottom: 18px;
        text-align: center;
      }

      input {
        padding: 11px;
        margin-bottom: 12px;
      }

      button {
        width: 64%;
        padding: 11px;
        font-size: 13px;
      }

      .category {
        ul {
          li {
            padding: 13px 0;
            font-size: 13px;
          }
        }
      }

      .recent-post {
        ul {
          li {
            padding: 6px 0;

            .recent__img {
              width: 18%;
            }

            .recent__info {
              width: 82%;

              .recent__title {
                margin-bottom: 8px;
                font-size: 13px;
                text-align: left;
                width: 100%;
              }

              .recent__time {
                color: #666;
                font-size: 10px;
              }
            }
          }
        }
      }

      .tag-clouds {
        .tags__content {
          padding: 15px 0;

          .tag {
            margin: 0;
            font-size: 12px;
            margin-right: 15px;
            margin-bottom: 12px;
          }
        }
      }

      .section-sticky {
        position: static;
      }

      .instagram-feeds {
        .instagram__content {
          padding-top: 10px;

          .img {
            width: 26%;
            margin-bottom: 8px;
          }
        }
      }

      .sub {
        text-align: center;
      }
    }
  }
}
@media #{$information-phone} {
  .blog__content {
    padding: 35px 0;
    flex-wrap: wrap;

    .posts {
      width: 100%;
      padding-bottom: 35px;
    }

    aside {
      width: 100%;
      margin-top: 40px;
      h3 {
        font-size: 17px;
        padding-bottom: 10px;
        color: var(--title-color);
      }
      section {
        padding: 15px;
        margin-bottom: 18px;
        box-shadow: 0 0 0px 1px var(--mode-border);
        text-align: center;
        padding-bottom: 0;
        background: var(--mode-background);
      }
      .search {
        padding-bottom: 15px;
      }
      input {
        padding: 11px;
        margin-bottom: 12px;
        background: var(--mode-background);
        color: var(--text-color);
        border: 1px solid var(--mode-border);
      }
      button {
        width: 38%;
        padding: 9px;
        font-size: 10px;
      }
      .category {
        ul {
          li {
            padding: 13px 0;
            color: var(--text-color);
            font-size: 13px;
          }
        }
      }
      .recent-post {
        ul {
          li {
