                margin-bottom: 8px;
                font-size: 13px;
                width: 100%;
                text-align: left;
                color: var(--title-color);
              }

              .recent__time {
                font-size: 10px;
                color: var(--text-color);
              }
            }
          }
        }
      }

      .tag-clouds {
        .tags__content {
          padding: 15px 0;

          .tag {
            margin: 0;
            font-size: 12px;
            margin-right: 15px;
            margin-bottom: 12px;
          }
        }
      }

      .section-sticky {
        position: static;
      }

      .instagram-feeds {
        .instagram__content {
          padding-top: 10px;

          .img {
            width: 26%;
            margin-bottom: 8px;
          }
        }
      }

      .sub {
        text-align: center;
      }
    }
  }
}
