.cardBox {
  position: fixed;
  width: 300px;
  height: 100%;
  background-color: var(--mode-background);
  right: 0;
  top: 0;
  z-index: 94;

  transform: translateX(100%);
  transition: transform 0.5s ease-in-out;

  &.active {
    transform: translateX(0%);
  }

  .cardBox__content {
    padding: 15px;
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
    height: 100%;
    padding-bottom: 14px;
    box-sizing: border-box;
    justify-content: center;

    ul {
      list-style: none;
      padding: 0 2px;
      margin: 0;
      height: 92%;
      overflow: auto;
      width: 100%;

      li {
        display: flex;
        box-shadow: 0 0 4px var(--mode-border);
        margin-bottom: 20px;

        .product__img {
          max-width: 81px;
          img {
            width: 100%;
          }
        }

        .product__info {
          padding-left: 16px;
          display: flex;
          align-items: center;
          flex-wrap: wrap;
          align-content: center;

          .name {
            font-size: 14px;
            width: 100%;
            margin-bottom: 8px;
            display: block;
            color: var(--title-color);
          }

          .price {
            color: var(--main-color);
            font-size: 15px;
            font-weight: 600;
            letter-spacing: 1px;
          }
        }
      }

      .noProducts {
        justify-content: center;
        text-transform: capitalize;
        font-size: 20px;
        padding-top: 40px;
        color: var(--title-color);
        padding-bottom: 15px;
      }
    }

    .nicescroll-rails-vr .nicescroll-cursors {
      width: 8px !important;
      background-color: var(--main-color) !important;
      border: none !important;
      border-radius: 0px !important;
    }

    .clear {
      text-transform: capitalize;
      border: none;
      text-decoration: none;
      color: #fff;
      background-color: var(--main-color);
      padding: 17px 0;
      font-weight: 600;
      width: 100%;
      letter-spacing: 2px;
      font-size: 15px;
      box-sizing: border-box;
      display: inline-block;
      cursor: pointer;
      outline: none;
    }
  }

  .cardBox__icon {
    position: absolute;
    top: 200px;
    left: 0;
    transform: translateX(-100%);
    padding: 9px 25px;
    padding-top: 29px;
    background-color: var(--mode-background);
    color: var(--main-color);
    box-shadow: -20px 0px 20px 0 rgba(0, 0, 0, 0.02);
    cursor: pointer;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      z-index: 55;
    }
    .count-products {
      position: absolute;
      top: 4px;
      right: 26px;
      font-size: 17px;
      padding: 4px 5px;
      font-weight: 700;
      color: var(--title-color);
    }

    .icon {
      font-size: 19px;
    }
  }
}
@media #{$information-desktops} {
  .cardBox {
    .cardBox__content {
      ul {
        height: 92%;
        li {
          margin-bottom: 10px;
          .product__info {
            padding-left: 10px;
            .name {
              font-size: 13px;
            }
            .price {
              font-size: 11px;
            }
          }
        }
        .noProducts {
          font-size: 14px;
          padding-top: 30px;
        }
      }
      .clear {
        font-size: 14px;
        padding: 14px 0;
      }
    }

    .cardBox__icon {
      top: 150px;
      padding: 7px 17px;
      padding-top: 30px;
      .count-products {
        top: 4px;
        right: 0;
        font-size: 18px;
        width: 100%;
        padding: 0;
        padding-top: 5px;
        text-align: center;
      }
      .icon {
        font-size: 15px;
      }
    }
  }
}

@media #{$information-tablet} {
