              font-size: 11px;
            }

            .price {
              font-size: 11px;
            }
          }
        }

        .noProducts {
          font-size: 11px;
          padding-top: 8px;
        }
      }

      .clear {
        font-size: 11px;
        padding: 15px 0;
      }
    }

    .cardBox__icon {
      top: 150px;
      padding: 7px 17px;
      padding-top: 25px;
      .count-products {
        top: 4px;
        right: 16px;
        font-size: 16px;
      }

      .icon {
        font-size: 12px;
      }
    }
  }
}

@media #{$information-phone} {
