        height: 92%;

        li {
          margin-bottom: 10px;

          .product__img {
            max-width: 65px;
          }

          .product__info {
            padding-left: 8px;
            padding-top: 7px;
            padding-bottom: 7px;

            .name {
              font-size: 10px;
            }

            .price {
              font-size: 9px;
            }
          }
        }

        .noProducts {
          font-size: 11px;
          padding-top: 8px;
        }
      }

      .clear {
        font-size: 10px;
        padding: 12px 0;
      }
    }

    .cardBox__icon {
      top: 100px;
      padding: 5px 13px;
      padding-top: 21px;

      .count-products {
        top: 6px;
        right: 10px;
        font-size: 12px;
      }

      .icon {
        font-size: 12px;
      }
    }
  }
}
@media (max-height: '340px') and (rientation: landscape) {
  .cardBox {
    width: 200px;

    .cardBox__content {
      padding: 7px;

      ul {
        height: 92%;

        li {
          margin-bottom: 10px;

          .product__img {
            max-width: 65px;
          }

          .product__info {
            padding-left: 8px;
            padding-top: 7px;
            padding-bottom: 7px;

            .name {
              font-size: 10px;
            }

            .price {
              font-size: 9px;
            }
          }
        }

        .noProducts {
          font-size: 11px;
          padding-top: 8px;
        }
      }

      .clear {
        font-size: 10px;
        padding: 11px 0;
      }
    }

    .cardBox__icon {
      top: 80px;
      padding: 2px 12px;
      padding-top: 18px;

      .count-products {
        top: 4px;
        right: 11px;
        font-size: 11px;
      }

      .icon {
        font-size: 11px;
      }
    }
  }
}
