.setting {
  width: 250px;
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  transform: translateX(-100%);
  z-index: 95;
  transition: transform 0.5s ease-in-out;
  padding: 20px;
  box-sizing: border-box;
  text-align: center;
  background-color: var(--mode-background);
  color: var(--mode-color);
  &.open {
    transform: translateX(0);
  }

  .setting__title {
    font-size: 20px;
    text-transform: capitalize;
    font-weight: 600;
    color: var(--title-color);
  }

  .setting__dark {
    padding: 20px 0px;
    box-shadow: 0 0 4px 0px var(--mode-border);

    section {
      margin-top: 16px;
    }

    @supports (-webkit-appearance: none) or (-moz-appearance: none) {
      input[type='checkbox'] {
        --active: #292929;
        --active-inner: #fff;
        --border: #292929;
        --background: #fff;
        --disabled: #f6f8ff;
        --disabled-inner: #e1e6f9;
        -webkit-appearance: none;
        -moz-appearance: none;
        height: 30px;
        outline: none;
        display: inline-block;
        vertical-align: top;
        position: relative;
        margin: 0;
        cursor: pointer;
        border: 1px solid #2929298f;
        background: var(--b, var(--background));
        transition: background 0.3s, border-color 0.3s, box-shadow 0.2s;

        &:after {
          content: '';
          display: block;
          left: 3px;
          top: 1px;
          position: absolute;
          transition: transform 0.3s ease-in-out;
        }

        &:checked {
          --b: var(--active);
          --bc: var(--active);
        }
      }

      input[type='checkbox'] {
        &.switch {
          width: 70px;
          border-radius: 18px;

          &:after {
            left: 2px;
            top: 50%;
            border-radius: 50%;
            width: 25px;
            height: 25px;
            background: var(--ab, var(--border));
            transform: translateX(var(--x, 0)) translateY(-50%);
          }

          &:checked {
            --ab: var(--active-inner);
            --x: 39px;
          }

          &:disabled {
            &:not(:checked) {
              &:after {
                opacity: 0.6;
              }
            }
          }
        }
      }
    }
  }

  .setting__themes {
    padding: 20px 0px;
    box-shadow: 0 0 4px 0px var(--mode-border);
    margin: 25px 0;

    .list-colors {
      padding: 0;
      list-style: none;
      display: flex;
      flex-wrap: wrap;
      margin: auto;
      margin-top: 15px;
      width: 73%;
      justify-content: space-between;

      li {
        width: 40px;
        height: 40px;
        display: inline-block;
        margin-bottom: 15px;
        box-shadow: 0 0 2px 0px var(--mode-border);
        cursor: pointer;
        opacity: 0.3;

        &.active {
          opacity: 1;
        }
      }
    }
  }

  .change-background {
    padding: 20px 0px;
    box-shadow: 0 0 4px 0px var(--mode-border);

    .setting__title {
      font-size: 18px;
    }

    .change__box {
      margin-top: 18px;
      display: flex;
      justify-content: space-around;

      button {
        overflow: visible;
        border: none;
        padding: 11px 29px;
        font-weight: 700;
        text-transform: uppercase;
        font-size: 14px;
        background: #2f9e2f;
        color: #fff;
        opacity: 0.4;
        outline: none;
        cursor: pointer;

        &.active {
          opacity: 1;
        }
      }
    }
  }

  .setting__icon {
    position: absolute;
    top: 200px;
    right: 0;
    padding: 15px 0;
    transform: translateX(100%);
    width: 67px;
    text-align: center;
    box-sizing: border-box;
    font-size: 23px;
    cursor: pointer;
    background-color: var(--mode-background);
    color: var(--main-color);
    box-shadow: 20px 0px 20px 0 rgba(0, 0, 0, 0.02);
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      z-index: 55;
    }
    .icon--cog {
      display: inline-block;
    }
  }
}
@media #{$information-desktops} {
  .setting {
    width: 180px;
    z-index: 99;
    padding: 14px;

    .setting__title {
      font-size: 14px;
      text-transform: capitalize;
      font-weight: 600;
    }

    .setting__dark {
      padding: 20px 0px;
      box-shadow: 0 0 4px 0px rgba(0, 0, 0, 0.1);

      section {
        margin-top: 10px;
      }

      @supports (-webkit-appearance: none) or (-moz-appearance: none) {
        input[type='checkbox'] {
          height: 20px;
        }

        input[type='checkbox'] {
          &.switch {
            width: 45px;

            &:after {
              width: 16px;
              height: 16px;
              transform: translateX(var(--x, 0)) translateY(-50%);
            }

            &:checked {
              --ab: var(--active-inner);
              --x: 23px;
            }
          }
        }
      }
    }

    .setting__themes {
      padding: 20px 0px;
      margin: 15px 0;

      .list-colors {
        width: 78%;

        li {
          width: 30px;
          height: 30px;
          margin-bottom: 12px;
        }
      }
    }

    .change-background {
      padding: 20px 0px;
      box-shadow: 0 0 4px 0px rgba(0, 0, 0, 0.1);

      .setting__title {
        font-size: 12px;
      }

      .change__box {
        margin-top: 13px;
        flex-wrap: wrap;

        button {
          width: 68%;
          margin-bottom: 8px;
          padding: 10px 24px;
          font-size: 10px;
        }
      }
    }

    .setting__icon {
      top: 150px;
      padding: 14px 0;
      width: 51px;
      font-size: 20px;
    }
  }
}
