.method {
  padding: 60px 0;
  .method__content {
    display: flex;
    color: #fff;
    background-color: var(--main-color);
    padding: 40px 0;
    .col {
      flex: 1;
      padding: 0px 31px;
      box-sizing: border-box;
      .method__icon {
        font-size: 41px;
      }
      h3 {
        font-size: 20px;
        color: #fff;
      }
      p {
        text-transform: capitalize;
        width: 83%;
        line-height: 1.6;
        font-size: 16px;
        margin: auto;
        color: #ececec;
      }
    }
  }
}
@media #{$information-large-desktops} {
  .method {
    padding: 45px 0;
    .method__content {
      padding: 50px 0;
      flex-wrap: wrap;
      justify-content: center;
      .col {
        flex: 1;
        padding: 0px 17px;
        .method__icon {
          font-size: 34px;
        }
        h3 {
          font-size: 18px;
        }
        p {
          width: 100%;
          margin: auto;
          line-height: 1.6;
          font-size: 15px;
          color: #ffffff;
        }
      }
    }
  }
}
@media #{$information-desktops} {
  .method {
    padding: 45px 0;
    .method__content {
      padding: 50px 0;
      flex-wrap: wrap;
      justify-content: center;
      .col {
        flex: 1;
        padding: 0px 17px;
        .method__icon {
          font-size: 29px;
        }
        h3 {
          font-size: 15px;
        }
        p {
          width: 100%;
          margin: auto;
          line-height: 1.6;
          font-size: 12px;
          color: #ffffff;
        }
      }
    }
  }
}
@media #{$information-tablet} {
  .method {
    padding: 35px 0;
    .method__content {
      padding: 25px 0;
      flex-wrap: wrap;
      justify-content: center;
      .col {
        flex: inherit;
        width: 50%;
        padding: 0px 16px;
        padding-bottom: 25px;
        text-align: center;
        .method__icon {
          font-size: 30px;
        }
        h3 {
          font-size: 18px;
        }
        p {
          width: 89%;
          margin: auto;
          line-height: 1.6;
          font-size: 13px;
          color: #ffffff;
        }
      }
    }
  }
}
@media #{$information-phone} {
  .method {
    padding: 35px 0;
    .method__content {
      padding: 16px 0;
      flex-wrap: wrap;
      .col {
        flex: 100%;
        padding: 0px 20px;
        margin-bottom: 25px;
        &:last-child {
          margin-bottom: 0;
        }
        .method__icon {
          font-size: 23px;
        }
        h3 {
          font-size: 16px;
        }
        p {
          width: 92%;
          line-height: 1.5;
          font-size: 13px;
          color: #ffffff;
        }
      }
    }
  }
}
