.details-product-box {
  width: 1207px;
  background: var(--mode-background);
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 555;
  transform: translate(-50%, -50%);
  display: none;
  padding: 30px;
  border-radius: 7px;
  overflow: auto;
  .close--box {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 35px;
    color: var(--main-color);
    z-index: 222;
    cursor: pointer;
  }
  .product--info {
    padding: 0;
    .product--details {
      display: flex;
      .product_images {
        width: 50%;
        .imgs-contailer {
          height: 560px;
          .img-box {
            padding: 17px 39px;
          }
        }
        .imgs-navigation {
          display: flex;
          height: 135px;
          .img-nav {
            box-sizing: border-box;
            padding: 5px;
            img {
              width: 100%;
              height: 100%;
            }
          }
        }
      }
      .product--details__data {
        padding: 45px 10px;
        width: 50%;
        padding-bottom: 0;
      }
    }
  }
}
@media #{$information-desktops} {
  .details-product-box {
    width: 480px;
    box-sizing: border-box;
    padding: 5px;
    height: 95%;
    .close--box {
      position: fixed;
      top: 14px;
      right: 10px;
      padding: 4px 8px;
      font-size: 15px;
    }
    .product--info {
      padding: 0;
      .product--details {
        display: flex;
        flex-wrap: wrap;
        .product_images {
          width: 100%;
