.page__interface {
  height: 570px;
  background-image: url('../img/hero/about_hero.webp');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;

  .h1 {
    font-size: 80px;
    text-transform: capitalize;
    text-align: center;
    font-weight: 600;
    color: #333;
  }
}
@media #{$information-desktops} {
  .page__interface {
    height: 470px;

    .h1 {
      font-size: 60px;
    }
  }
}
@media #{$information-tablet} {
  .page__interface {
    height: 310px;

    .h1 {
      font-size: 37px;
    }
  }
}

@media #{$information-phone} {
  .page__interface {
    height: 250px;

    .h1 {
      font-size: 30px;
    }
  }
}
