.interface {
  position: relative;
  background: #000;
  .interface_overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
    z-index: 18;
  }
  .owl-item {
    height: calc(100vh - 112px);
    img {
      height: 100%;
      width: 100%;
    }
  }
  .owl-dots {
    display: none;
  }
  .icon-down {
    position: absolute;
    bottom: 60px;
    left: 50%;
    transform: translateX(-50%);
    color: #fff;
    font-size: 27px;
    z-index: 20;
    cursor: pointer;
    outline: none;
  }
  .interface_info {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    color: #fff;
    padding: 21px;
    font-size: 14px;
    z-index: 20;
    h1 {
      color: #fff;
      margin: 0;
      margin-bottom: 17px;
      line-height: 1.5;
      font-size: 67px;
      width: 72%;
    }
    .interface_button {
      background: var(--main-color);
      color: #fff;
      text-decoration: none;
      padding: 17px 45px;
      display: inline-block;
      font-size: 14px;
      font-weight: 700;
      text-transform: uppercase;
    }
  }
  .watch {
    width: 560px;
    height: 550px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    overflow: hidden;
    z-index: 20;
    img {
      width: 100%;
      height: 100%;
      transform: scale(1);
      animation: scale 3s linear 0s infinite;
    }
    @keyframes scale {
      0% {
        transform: scale(1);
      }

      25% {
        transform: scale(1.02);
      }

      50% {
        transform: scale(1.04);
      }

      75% {
        transform: scale(1.02);
      }

      100% {
        transform: scale(1);
      }
    }
  }
}
