      height: calc(100vh - 77px);
      img {
        height: 100%;
        width: 100%;
      }
    }
    .icon-down {
      bottom: 35px;
      font-size: 20px;
    }
    .interface_info {
      h1 {
        font-size: 47px;
      }
      .interface_button {
        padding: 14px 32px;
        font-size: 12px;
      }
    }
    .watch {
      display: none;
